
@font-family: "Segoe UI", sans-serif;
@primary-bg: #f8f8f8;
@text-color: #333;

.root {
    font-family: @font-family;
    font-size: 13px;
    background-color: @primary-bg;
    color: @text-color;
}

.chat-input {
    width: 100%;
    font-size: 14px;
    line-height: 1.4;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    min-height: 32px;
    max-height: 200px; // limite m√°ximo de crescimento
    overflow-y: auto;
    resize: none;
}

button {
  background-color: #83898d;
  border: none;
  color: rgb(92, 86, 86);
  padding: 6px 16px;
  margin-top: 8px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  border-radius: 2px;
  transition: background-color 0.2s ease;

  &:hover {
    background-color: #106ebe;
  }

  &:active {
    background-color: #005a9e;
  }

  &:disabled {
    background-color: #ffffff;
    cursor: default;
  }
}

.chat-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.chat-output {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  word-break: break-word;
  white-space: pre-wrap;
  padding-top: 8px;
}

pre {
  white-space: pre-wrap;
  word-break: break-word;
  overflow-x: auto;
}
